/** @format */

import React, { useState } from "react";
import { NavData } from "./NavData";
import { Link as Scroll } from "react-scroll";

const Navigation = () => {
  const [burger_class, setBugerClass] = useState("bar unclicked");
  const [menu_class, setMenuClass] = useState("menu hidden");
  const [isMenuClicked, setIsMenuClicked] = useState(false);

  const updateMenu = () => {
    if (!isMenuClicked) {
      setBugerClass("bar clicked");
      setMenuClass("menu visible");
      setIsMenuClicked(true);
    } else {
      setBugerClass("bar unclicked");
      setMenuClass("menu hidden");
      setIsMenuClicked(false);
    }
  };

  return (
    <div className="burger" onClick={updateMenu}>
      <div className="burger_box">
        <div className={burger_class}></div>
        <div className={burger_class}></div>
        <div className={burger_class}></div>
      </div>

      <div className={menu_class}>
        <ul className="navbar_menu_list">
          {NavData.map((value, key) => {
            return (
              <Scroll
                to={value.link}
                smooth={true}
                duration={700}
                onClick={updateMenu}
              >
                <li
                  key={key}
                  id={window.location.pathname == value.link ? "active" : ""}
                  className="row"
                >
                  <a href={value.link} id="title">
                    {value.title}
                  </a>
                </li>
              </Scroll>
            );
          })}
        </ul>
      </div>
    </div>
  );
};

export default Navigation;
